*{
  color: var(--main-font-color);
  font-size:11pt;
  font-family: 'Montserrat', sans-serif;
}
.brand{
  padding:0 15px;
  margin:0;
  height:100%;
  display:flex;
  justify-content: space-between;
  align-items: center;
  span{
    color: var(--main-primary-color);
    text-decoration: none !important;
  }
  svg{
    path{
      fill: var(--main-primary-color);
    }
    circle{
      fill: var(--main-primary-color);
    }
  }
}
